export default "<!doctype html><html lang=en><head><meta charset=utf-8><title>__TITLE__</title><link rel=icon href=https://raw.githubusercontent.com/graphql-hive/graphql-yoga/refs/heads/main/website/src/app/favicon.ico><link crossorigin rel=stylesheet href=https://unpkg.com/@graphql-yoga/graphiql@4.4.3/dist/graphiql.css></head><body id=body class=no-focus-outline><noscript>You need to enable JavaScript to run this app.</noscript><div id=root>Loading __TITLE__...</div><script>function prepareBlob(r){const o=new Blob([r],{type:\"application/javascript\"});return URL.createObjectURL(o)}const workers={},workerUrls={editorWorkerService:\"https://unpkg.com/@graphql-yoga/graphiql@4.4.3/dist/monacoeditorwork/editor.worker.bundle.js\",json:\"https://unpkg.com/@graphql-yoga/graphiql@4.4.3/dist/monacoeditorwork/json.worker.bundle.js\",graphql:\"https://unpkg.com/@graphql-yoga/graphiql@4.4.3/dist/monacoeditorwork/graphql.worker..bundle.js\"};function prepareWorkers(){return Promise.all(Object.entries(workerUrls).map(async([r,o])=>{const e=await fetch(o),t=await e.text();workers[r]=prepareBlob(t)}))}self.MonacoEnvironment={globalAPI:!1,getWorkerUrl:function(r,o){return workers[o]}}</script><script src=https://unpkg.com/@graphql-yoga/graphiql@4.4.3/dist/yoga-graphiql.umd.js></script><script>prepareWorkers().finally(()=>{YogaGraphiQL.renderYogaGraphiQL(root,__OPTS__)})</script></body></html>";
